    if (typeof id === "number") {
      if (id >= this.meshes.length) {
        return -1;
      }
      return id;
    }
    const n = this.meshes.length;
    for (let i = 0; i < n; i++) {
      const id_i = this.meshes[i].id;
      if (id_i === id) {
        return i;
      }
    }
    return -1;
